"use client"

import type React from "react"
import { useState, useRef } from "react"
import { Sparkles, Code, Eye, Upload, Wand2, Save, RefreshCw, Menu, X, Zap, Star, Download } from "lucide-react"

export default function Home() {
  const [command, setCommand] = useState("")
  const [userId] = useState("default-user")
  const [generatedHtml, setGeneratedHtml] = useState("")
  const [templateId, setTemplateId] = useState("")
  const [changeQuery, setChangeQuery] = useState("")
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [loading, setLoading] = useState(false)
  const [activeTab, setActiveTab] = useState<"preview" | "code">("preview")
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleGenerate = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setGeneratedHtml("")

    // Simulate API call
    setTimeout(() => {
      const mockHtml = `
        <div class="max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden">
          <div class="p-6">
            <h2 class="text-2xl font-bold text-gray-900 mb-4">Contact Form</h2>
            <form class="space-y-4">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Name</label>
                <input type="text" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="Your name">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Email</label>
                <input type="email" class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="your@email.com">
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-1">Message</label>
                <textarea class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" rows="4" placeholder="Your message"></textarea>
              </div>
              <button type="submit" class="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors">Send Message</button>
            </form>
          </div>
        </div>
      `
      setGeneratedHtml(mockHtml)
      setLoading(false)
    }, 2000)
  }

  const handleUpdateUI = async () => {
    if (!changeQuery.trim() || !generatedHtml) return
    setLoading(true)

    setTimeout(() => {
      setChangeQuery("")
      setLoading(false)
    }, 1500)
  }

  const saveUI = async (feedback: boolean) => {
    alert("UI saved successfully!")
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-purple-950/20 to-slate-950 relative overflow-hidden">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-purple-500/10 rounded-full blur-3xl animate-pulse"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-blue-500/10 rounded-full blur-3xl animate-pulse delay-1000"></div>
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-cyan-500/5 rounded-full blur-3xl animate-pulse delay-500"></div>
      </div>

      {/* Header */}
      <header className="relative z-10 backdrop-blur-xl bg-slate-950/80 border-b border-slate-800/50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-3">
              <div className="relative">
                <div className="w-10 h-10 bg-gradient-to-tr from-blue-500 via-purple-500 to-cyan-500 rounded-xl flex items-center justify-center shadow-lg">
                  <Sparkles className="w-5 h-5 text-white" />
                </div>
                <div className="absolute -top-1 -right-1 w-3 h-3 bg-green-400 rounded-full animate-pulse"></div>
              </div>
              <div>
                <h1 className="text-xl font-bold bg-gradient-to-r from-white to-slate-300 bg-clip-text text-transparent">
                  AI UI Generator
                </h1>
                <p className="text-xs text-slate-400">Powered by AI</p>
              </div>
            </div>

            {/* Desktop Navigation */}
            <nav className="hidden md:flex items-center space-x-8">
              <a
                href="#"
                className="text-slate-300 hover:text-white transition-colors duration-200 flex items-center space-x-1"
              >
                <span>Docs</span>
              </a>
              <a
                href="#"
                className="text-slate-300 hover:text-white transition-colors duration-200 flex items-center space-x-1"
              >
                <Star className="w-4 h-4" />
                <span>Examples</span>
              </a>
              <a href="#" className="text-slate-300 hover:text-white transition-colors duration-200">
                API
              </a>
              <div className="w-px h-6 bg-slate-700"></div>
              <button className="px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition-all duration-200 shadow-lg hover:shadow-xl">
                Sign In
              </button>
            </nav>

            {/* Mobile menu button */}
            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="md:hidden p-2 rounded-lg bg-slate-800/50 text-slate-300 hover:text-white transition-colors"
            >
              {mobileMenuOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
            </button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {mobileMenuOpen && (
          <div className="md:hidden border-t border-slate-800/50 bg-slate-950/95 backdrop-blur-xl">
            <div className="px-4 py-4 space-y-3">
              <a href="#" className="block text-slate-300 hover:text-white transition-colors">
                Docs
              </a>
              <a href="#" className="block text-slate-300 hover:text-white transition-colors">
                Examples
              </a>
              <a href="#" className="block text-slate-300 hover:text-white transition-colors">
                API
              </a>
              <button className="w-full mt-4 px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg">
                Sign In
              </button>
            </div>
          </div>
        )}
      </header>

      {/* Main Content */}
      <main className="relative z-10 flex-1 flex flex-col items-center justify-center px-4 py-8">
        {!generatedHtml && !loading && (
          <div className="text-center mb-16 max-w-4xl mx-auto">
            <div className="inline-flex items-center px-4 py-2 bg-gradient-to-r from-blue-500/10 to-purple-500/10 border border-blue-500/20 rounded-full text-blue-300 text-sm font-medium mb-6">
              <Zap className="w-4 h-4 mr-2" />
              AI-Powered UI Generation
            </div>
            <h2 className="text-4xl md:text-6xl font-bold bg-gradient-to-r from-white via-slate-200 to-slate-400 bg-clip-text text-transparent mb-6 leading-tight">
              Create Beautiful UIs
              <br />
              <span className="bg-gradient-to-r from-blue-400 via-purple-400 to-cyan-400 bg-clip-text text-transparent">
                In Seconds
              </span>
            </h2>
            <p className="text-xl text-slate-400 mb-8 max-w-2xl mx-auto leading-relaxed">
              Describe your vision and watch as AI transforms your ideas into pixel-perfect, responsive user interfaces.
            </p>
            <div className="flex flex-wrap justify-center gap-4 text-sm text-slate-500">
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-green-400 rounded-full"></div>
                <span>React Components</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-blue-400 rounded-full"></div>
                <span>Tailwind CSS</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-2 h-2 bg-purple-400 rounded-full"></div>
                <span>Responsive Design</span>
              </div>
            </div>
          </div>
        )}

        {/* Results Section */}
        {(generatedHtml || loading) && (
          <div className="w-full max-w-5xl mt-8 mb-32">
            <div className="backdrop-blur-xl bg-slate-900/50 rounded-3xl shadow-2xl border border-slate-700/50 overflow-hidden">
              {/* Tabs */}
              <div className="border-b border-slate-700/50 flex bg-slate-800/30">
                <button
                  onClick={() => setActiveTab("preview")}
                  className={`flex-1 py-4 text-lg font-medium flex items-center justify-center gap-3 transition-all duration-300 ${
                    activeTab === "preview"
                      ? "bg-gradient-to-r from-blue-600/20 to-purple-600/20 text-white border-b-2 border-blue-400"
                      : "text-slate-400 hover:text-white hover:bg-slate-700/30"
                  }`}
                >
                  <Eye className="w-5 h-5" />
                  Preview
                </button>
                <button
                  onClick={() => setActiveTab("code")}
                  className={`flex-1 py-4 text-lg font-medium flex items-center justify-center gap-3 transition-all duration-300 ${
                    activeTab === "code"
                      ? "bg-gradient-to-r from-purple-600/20 to-pink-600/20 text-white border-b-2 border-purple-400"
                      : "text-slate-400 hover:text-white hover:bg-slate-700/30"
                  }`}
                >
                  <Code className="w-5 h-5" />
                  Code
                </button>
              </div>

              {/* Content */}
              <div className="p-8 bg-slate-950/30 min-h-[500px]">
                {loading ? (
                  <div className="flex flex-col items-center justify-center py-24">
                    <div className="relative mb-8">
                      <div className="w-16 h-16 border-4 border-slate-700 rounded-full"></div>
                      <div className="absolute top-0 left-0 w-16 h-16 border-4 border-blue-500 rounded-full border-t-transparent animate-spin"></div>
                    </div>
                    <div className="text-center">
                      <h3 className="text-xl font-semibold text-white mb-2">Generating your UI...</h3>
                      <p className="text-slate-400">This usually takes a few seconds</p>
                    </div>
                  </div>
                ) : (
                  <div className="min-h-[500px]">
                    {activeTab === "preview" ? (
                      <div className="relative">
                        <div className="absolute top-4 right-4 z-10 flex space-x-2">
                          <button className="p-2 bg-slate-800/80 backdrop-blur-sm text-slate-300 hover:text-white rounded-lg transition-colors">
                            <Download className="w-4 h-4" />
                          </button>
                        </div>
                        <div className="border border-slate-600/50 rounded-xl overflow-hidden bg-white shadow-2xl">
                          <iframe
                            srcDoc={generatedHtml}
                            className="w-full h-[500px] bg-white"
                            title="UI Preview"
                            sandbox="allow-same-origin"
                          />
                        </div>
                      </div>
                    ) : (
                      <div className="relative">
                        <div className="absolute top-4 right-4 z-10">
                          <button className="p-2 bg-slate-800/80 backdrop-blur-sm text-slate-300 hover:text-white rounded-lg transition-colors">
                            <Download className="w-4 h-4" />
                          </button>
                        </div>
                        <pre className="bg-slate-900/80 text-green-400 p-6 rounded-xl overflow-x-auto text-sm font-mono border border-slate-700/50 shadow-inner">
                          {generatedHtml}
                        </pre>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Refine/Update UI */}
        {generatedHtml && !loading && (
          <div className="w-full max-w-4xl mb-36">
            <div className="backdrop-blur-xl bg-slate-900/50 rounded-2xl shadow-xl border border-slate-700/50 p-8">
              <div className="flex items-center space-x-3 mb-6">
                <Wand2 className="w-6 h-6 text-purple-400" />
                <h3 className="text-xl font-semibold text-white">Refine Your UI</h3>
              </div>
              <div className="flex flex-col sm:flex-row gap-4">
                <input
                  type="text"
                  value={changeQuery}
                  onChange={(e) => setChangeQuery(e.target.value)}
                  placeholder="Describe changes (e.g., 'Make the button blue and larger')"
                  className="flex-1 px-6 py-4 border border-slate-600/50 rounded-xl bg-slate-800/50 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500/50 transition-all duration-200 backdrop-blur-sm"
                />
                <div className="flex gap-3">
                  <button
                    onClick={handleUpdateUI}
                    disabled={!changeQuery.trim()}
                    className="px-8 py-4 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 font-medium shadow-lg hover:shadow-xl"
                  >
                    Update
                  </button>
                  <button
                    onClick={() => saveUI(true)}
                    className="flex items-center space-x-2 px-6 py-4 bg-gradient-to-r from-green-600 to-emerald-600 text-white rounded-xl hover:from-green-700 hover:to-emerald-700 transition-all duration-200 font-medium shadow-lg hover:shadow-xl"
                  >
                    <Save className="w-4 h-4" />
                    <span>Save</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </main>

      {/* Input Bar at Bottom */}
      <form
        onSubmit={handleGenerate}
        className="fixed bottom-0 left-0 w-full backdrop-blur-xl bg-slate-950/90 border-t border-slate-800/50 px-4 py-6 z-50"
        style={{ boxShadow: "0 -8px 32px 0 rgba(0,0,0,0.3)" }}
      >
        <div className="max-w-4xl mx-auto flex items-center gap-4">
          <button
            type="button"
            onClick={() => fileInputRef.current?.click()}
            className="flex items-center justify-center w-14 h-14 rounded-xl bg-slate-800/80 hover:bg-slate-700/80 transition-all duration-200 backdrop-blur-sm border border-slate-700/50 group"
            title="Upload reference image"
          >
            <Upload className="w-6 h-6 text-slate-400 group-hover:text-white transition-colors" />
          </button>

          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            onChange={(e) => setImageFile(e.target.files?.[0] || null)}
            className="hidden"
          />

          <div className="flex-1 relative">
            <input
              type="text"
              value={command}
              onChange={(e) => setCommand(e.target.value)}
              placeholder="Describe your UI (e.g., 'Modern login form with logo')"
              className="w-full px-6 py-4 text-lg border border-slate-700/50 rounded-xl bg-slate-800/80 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500/50 focus:border-blue-500/50 transition-all duration-200 backdrop-blur-sm pr-4"
            />
            {imageFile && (
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                <div className="w-8 h-8 bg-green-500/20 border border-green-500/50 rounded-lg flex items-center justify-center">
                  <span className="text-xs text-green-400 font-medium">IMG</span>
                </div>
              </div>
            )}
          </div>

          <button
            type="submit"
            disabled={loading || !command.trim()}
            className="flex items-center gap-3 px-8 py-4 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-xl font-semibold text-lg hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 shadow-lg hover:shadow-xl min-w-[140px] justify-center"
          >
            {loading ? (
              <>
                <RefreshCw className="w-5 h-5 animate-spin" />
                <span>Generating...</span>
              </>
            ) : (
              <>
                <Sparkles className="w-5 h-5" />
                <span>Generate</span>
              </>
            )}
          </button>
        </div>
      </form>
    </div>
  )
}
